img {
  max-width: 100%;
  height: auto;
}

.page {
  height: 100%;
}

.page-main {
  flex-grow: 1;
}

.logo {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: flex-end;
  margin-right: 9px;
}
@media (max-width: 767px) {
  .logo {
    width: 100%;
  }
}

.logo__main-image {
  display: block;
  width: 34px;
  height: 38px;
  background-position: center;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .logo__main-image {
    background-image: url("/img/components/page-header__logo--mobile-cat.svg");
    margin-right: auto;
  }
}
@media (min-width: 768px) {
  .logo__main-image {
    width: 174px;
    height: 52px;
    background-image: url("/img/components/page-header__logo--tablet.svg");
  }
}
@media (min-width: 1440px) {
  .logo__main-image {
    width: 202px;
    height: 59px;
    background-image: url("/img/components/page-header__logo--desktop.svg");
  }
}

.logo__secondary-image {
  display: none;
}
@media (max-width: 767px) {
  .logo__secondary-image {
    position: absolute;
    display: block;
    top: 24px;
    left: 50%;
    transform: translateX(-50%);
    width: 101px;
    height: 18px;
    background-image: url("/img/components/page-header__logo--mobile-text.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.page-body {
  margin: 0 auto;
  background-color: #E5E5E5;
  min-width: 320px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
