.functional-nutrition {
  position: relative;
  background-color: #68b738;
}
@media (min-width: 768px) and (max-width: 1440px) {
  .functional-nutrition {
    height: 810px;
    background-color: transparent;
  }
  .functional-nutrition::after {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    width: 709px;
    height: 609px;
    background-image: url("/img/components/functional-nutrition__can--tablet.png");
    /* background-color: bisque; */
    background-position: center;
    background-repeat: no-repeat;
    /*       z-index: 0; */
  }
}
@media (min-width: 1440px) {
  .functional-nutrition {
    background-color: transparent;
    height: 553px;
    /*     background-color: $bg-color-main; */
    /*     background-color: hsla(0, 13%, 53%, 0.295); */
  }
  .functional-nutrition::before {
    position: absolute;
    bottom: 0;
    right: -110px;
    content: "";
    width: 720px;
    height: 720px;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: 0;
  }
  .functional-nutrition::after {
    position: absolute;
    top: 0;
    right: 220px;
    content: "";
    width: 552px;
    height: 500px;
    background-image: url("/img/components/functional-nutrition__can--desktop.png");
    /* background-color: bisque; */
    background-position: center;
    background-repeat: no-repeat;
  }
}

@media (max-width: 767px) {
  .functional-nutrition__wrapper {
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 768px) {
  .functional-nutrition__wrapper {
    margin-left: 87px;
  }
}
@media (min-width: 1440px) {
  .functional-nutrition__wrapper {
    margin-left: 80px;
  }
