.how-it-works {
  margin: 0;
}
.how-it-works__wrapper {
  display: flex;
  flex-direction: column;
}

.how-it-works__title {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  margin-top: 25px;
}
@media (min-width: 768px) {
  .how-it-works__title {
