  .how-it-works__list {
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
  }
}

.how-it-works__item {
  /*   position: relative; */
  z-index: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  min-height: 60px;
  display: flex;
}
@media (min-width: 768px) {
  .how-it-works__item {
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 235px;
  }
  .how-it-works__item::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 136px;
    height: 235px;
    background-image: url(../img/number1.svg);
    background-position: top;
    background-repeat: no-repeat;
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .how-it-works__item::before {
    left: auto;
    transform: none;
  }
}

/* .how-it-works__item:not(:last-child) {
