.main-nav {
  /* display: none; */
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
    z-index: 1;
  }
}

.main-nav__toggle {
  /*   margin-bottom: 7px; */
}
@media (max-width: 767px) {
  .main-nav__toggle {
    cursor: pointer;
    width: 24px;
    height: 18px;
    background-color: transparent;
    background-image: url(../img/components/main-nav__toggle--burger.svg);
    background-position: center;
    background-repeat: no-repeat;
    border: none;
  }
  .main-nav__list--opened ~ .main-nav__toggle {
    width: 20px;
    height: 20px;
    background-image: url(../img/components/main-nav__toggle--close.svg);
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    position: absolute;
    top: 66px;
    left: 0;
    width: 100vw;
    background-color: #ffffff;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .main-nav__list.main-nav__list--nojs {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav__list.main-nav__list--closed {
    display: none;
  }
}

.main-nav__item {
  padding-top: 22px;
  padding-bottom: 22px;
  position: relative;
}
@media (max-width: 767px) {
  .main-nav__item {
    border-top: 1px solid #E6E6E6;
  }
  .main-nav__item:last-child {
    border-bottom: 1px solid #E6E6E6;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    width: max-content;
    border-color: transparent;
  }
  .main-nav__item:not(:first-child) {
    margin-left: 34px;
  }
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item--current::after {
    background-color: #ffffff;
  }
}

